/* border-box */
.search-container *,
.search-container *:after,
.search-container :before {
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

/* search bar focus */
.search-container :focus {
  background: #fbfbfb;
  color: #333333;
  outline: 0;
}

/* search bar container */
.search-container {
  display: table;
  position: relative;
  width: 7.5rem;
  font-size: 1rem;
}

/* search icon button */
.search-icon-btn {
  background-color: rgb(0, 27, 77);
  border: 1px solid rgb(63, 107, 187);
  display: table-cell;
  height: 50px;
  position: relative;
  text-align: center;
  vertical-align: middle;
  color: #fff;
  z-index: 2;
}

/* search bar input container */
.search-input {
  position: absolute;
  left: 0;
  z-index: 1;
}

/* search bar input */
.search-input input.search-bar {
  border: 1px solid #cccccc;
  height: 50px;
  padding: 0px;
  width: 7.5rem;

  text-align: center;
}
.search-input input.search-bar:focus {
  padding-left: 60px;
  padding-right: 10px;
  width: 25rem;
}

/* transition effect */
.search-input input.search-bar,
.search-icon-btn {
  transition: all 0.5s;
}

.search-container:hover > .search-input input.search-bar {
  padding-left: 60px;
  padding-right: 10px;
  width: 25rem;
}

